import { formDate,setSStore,getCityCode } from '@/utils/util'
import FrontAndBack from '@/components/FrontAndBack.vue'
import SortSign from '@/components/SortSign.vue'
import Load from '@/components/Load.vue'
import TrainCityInput from '@/components/Train-city'

export default {
	data() {
		return {			
			query:{
				fromStation:null,
				arrStation:null,
				departTime:null,
			},
			loading:false,
			data:null,
		}
	},
	computed: {
		noRes(){
			if(this.data==null){
				return false;
			}
			return true;
		}
	},
	watch:{
		
	},
	methods: {
		search(){	
			this.query.departTime = formDate(this.query.departTime);
			this.shopping(this.query);	
		},
		booking(item){				
			let that = this;
			that.loading = true;
			let params = {
				"trainCode":item.train_code,
				"departTime":formDate(this.query.departTime),
				"fromStation":item.from_station,
				"arrStation":item.arrive_station
			}
			//验证 余票 
			function checkTicket(){
				that.$http.post("/train/checkTicket",params)
					.then((res) => {
						that.loading = false;
						let status = res.data.status;
						if(status==1001){
							let resData = res.data.data;
							resData.travel_time = formDate(that.query.departTime);
							setSStore('train_checkTicket',resData)
							g.next();							
						}else{
							
			                that.$notify.error({
			                    title: '验证余票失败',
			                    message: res.data.message
			                });
						}						
					})
			}
			function* gen(){
				yield checkTicket();
				that.$router.push({path:'/train/order'})
			}
			let g = gen()
			g.next()
		},
		
		//前一天//后一天
		getDate(Date){
			this.query.departTime = Date;
			this.search();
		},
		shopping(params){			
			this.loading = true;
			this.$http.post("/train/search",params)
				.then((res) => {
					this.loading = false;
					let status = res.data.status;
					if(status&&status==1001){
						let train_data = res.data.data.train_data;
						this.data = train_data;
						//列车为空
						if(train_data.length==0){
							this.data = null;
							this.$message("搜索的列车为空");
							return;
						}	
						return;
					}
					this.$notify.error({
	                    title: '查询失败',
	                    message: res.data.data.message
	                });
					this.data = null;
				})
				.catch((res) => {
					this.loading = false;
					this.data = null;
					this.$notify.error({
	                    title: '查询失败',
	                    message: "请检查网络连接，或更换搜索条件"
	                });
				})
		}
	},
	components:{
		FrontAndBack,
		SortSign,
		Load,
		TrainCityInput,
	},
	created() {
		//获取路由中的查询条件
		let query = {...this.$route.query};
		//查询
		if(JSON.stringify(query) != '{}'){
			this.shopping(query);
		}	
		//赋值给当前页面
		Object.assign(this.query,query);
		
	}
}


