<template>
	<div id="shopping">
		<!--加载层-->
		<Load v-if="loading"/>
		<div class="main">
			<div class="queryWarp">
				<div class="queryInput">
					<span>
						<TrainCityInput v-model="query.fromStation" class="inputText"/>
					</span>
					<span class="iconfont ex">&#xe61c;</span>
					<span>
						<TrainCityInput v-model="query.arrStation" class="inputText"/>
					</span>					
					<span class="deptDate">
						<span class="deptDateText">出发日 </span>
						<el-date-picker
					        v-model="query.departTime"
					        type="date"
					        size="small"
					        placeholder="选择日期">
					    </el-date-picker>
					
					</span>
					<span class="deptDate">
						<span class="deptDateText">出发时间 </span>
						<el-time-select
							size="small"
							:picker-options="{
							    start: '08:30',
							    step: '00:15',
							    end: '18:30'
							}"
							placeholder="选择时间">
						</el-time-select>						
					</span>
				</div>
				<div>
					<el-button @click="search" class="search" size="small">搜索</el-button>
				</div>
			</div>
			<div class="trainTypeWarp">
				<span>车次类型</span>
				<span class="noLimit">不限</span>			
				<span><el-checkbox>GC-高铁/城际</el-checkbox></span>
				<span><el-checkbox>D-动车</el-checkbox></span>
				<span><el-checkbox>Z-直达</el-checkbox></span>
				<span><el-checkbox>T-特快</el-checkbox></span>
				<span><el-checkbox>K-特快</el-checkbox></span>
				<span><el-checkbox>其他</el-checkbox></span>
			</div>
			<!--火车列表部分-->
			<div class="content" v-if="noRes">
				<div class="handle">
					<FrontAndBack @getDate="getDate" :date='query.departTime'></FrontAndBack>
					<div class="sort">
						<div class="flex">
							<span>出发时间</span>
							<sortSign></sortSign>
						</div>
						<div class="flex">
							<span>到达时间</span>
							<sortSign></sortSign>
						</div>
						<div class="flex">
							<span>历时</span>
							<sortSign></sortSign>
						</div>
						<div>共99个车次</div>
					</div>
				</div>
				<div class="table">
					<ul class="tableHead">
						<li>车次</li>
						<li class="site">出发站</br>到达站</li>
						<li>出发时间</br>到达时间</li>
						<li class="take">历时</li>
						<li>商务座</br>特等座</li>
						<li>一等座</li>
						<li>二等座</li>
						<li>高级</br>软卧</li>
						<li>软卧</li>
						<li>动卧</li>
						<li>硬卧</li>
						<li>软座</li>
						<li>硬座</li>
						<li>无座</li>
						<li>其他</li>
						<li>备注</li>
					</ul>
					<ul v-for="item in data">
						<li><b>{{item.train_code}}</b></li>
						<li class="site city">
							<div>
								<span class="iconfont start">始</span>
								<b>{{item.from_station}}</b>
							</div>
							<div>
								<span class="iconfont end">终</span>
								<b>{{item.arrive_station}}</b>
							</div>
						</li>
						<li>
							<p><b>{{item.from_time}}</b></p>
							<p>{{item.arrive_time}}</p>
						</li>
						<li class="take">
							<p><b>{{item.cost_time}}</b></p>
							<!--<p><b>当日到达</b></p>-->
						</li>
						<li><span>{{item.swz_num}}/{{item.tdz_num}}</span></li>
						<li><span>{{item.rz1_num}}</span></li>
						<li><span>{{item.rz2_num}}</span></li>
						<li>{{item.gw_num}}</span></li>
						<li>{{item.rw_num}}</span></li>
						<li>??</span></li>
						<li>{{item.yw_num}}</span></li>
						<li>{{item.rw_num}}</span></li>
						<li>{{item.yz_num}}</span></li>
						<li>{{item.wz_num}}</span></li>
						<li>{{item.rz2_num}}</span></li>
						<li>
							<el-button @click="booking(item)" class="book">预订</el-button>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import main from './main';
	export default {
		...main
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../../style/mixin.scss';
	@import "./style.scss";
</style>


